@import './sass/tailwind';

@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

body {
  margin: 0;
}

* {
  font-family: 'Ubuntu', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  width: 1.5rem;
  height: 1.5rem;
}

::-webkit-scrollbar-button {
  width: 0.75rem;
  height: 0.75rem;
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.30);
  border: 0.5rem solid transparent;
  border-radius: 40px;
}
::-webkit-scrollbar-thumb:hover {
  box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.40);
}
::-webkit-scrollbar-thumb:active {
  box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.50);
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.20);
  border: 0.5rem solid transparent;
  border-radius: 100px;
}
::-webkit-scrollbar-track:hover {
  box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.25);
}
::-webkit-scrollbar-track:active {
  box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.30);
}
::-webkit-scrollbar-corner {
  background: transparent;
}

@media (max-width: 767px) {
  ::-webkit-scrollbar {
    width: 0.75rem;
    height: 0.75rem;
  }
  ::-webkit-scrollbar-thumb, ::-webkit-scrollbar-track {
    border-left-width: 0;
    border-right-width: 0;
  }
}